@import '~antd/dist/antd.css';
@font-face {
  font-family: 'Skia';
  src: url('./font/Skia.ttf');
}

#root{
  height: 100%;
  width: 100%;
}

.pageContent{
  background-color: #158BC4;
  width: 100%;
  height: auto;
  padding: 5%;
}

.mainContent{
  background-color: white;
  width: 100%;
  height: auto;
  border-radius: 24px;
}

.header{
  font-family: "Skia";
  color: #158BC4;
  font-size: 30px;
  text-align: center;
  width: 100%;
  height: 50px;
  line-height: 160px;
  margin-bottom: 80px;
  font-style: bold;
}

.body{
  width: 100%;
  height: 90%;
  display: flex;
  padding: 1%;
  align-items: center;
}

.left{
  width: 50%;
  height: 100%;
}

.leftImage{
  width: 90%;
  height: auto;
  margin: auto 5%;
}

.right{
  width: 50%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-flow:row wrap;
}

.iconItem{
  width: 46%;
  height: auto;
  padding: 4%;
}

.groupOne{
  width: 100%;
  height: auto;
  background-color: #00DA36;
  border-radius: 10%;
}

.groupTwo{
  width: 100%;
  height: auto;
  background-color: #330567;
  border-radius: 10%;
}

.groupThree{
  width: 100%;
  height: auto;
  background-color: #AAC9D4;
  border-radius: 10%;
}

.groupFour{
  width: 100%;
  height: auto;
  background-color: #0DDA81;
  border-radius: 10%;
}

.iconImg{
  width: 80%;
  height: auto;
  margin: 10%;
}

.iconName{
  text-align: center;
  font-family: 'Skia';
  font-size: 20px;
  color: #158BC4;
  margin-top: 10px;
}

.ant-modal-header{
  font-family: 'Skia';
}

.vaccineSection{
  height:100%;
  width: 100%;
  margin: auto 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
}

.vaccineItem{
  width: 18%;
  height: 300px;
  padding: 10px;
  margin: 1%;
  background-image: url('./assets/vaccine.png');
  background-size: cover;
  background-position: 100%;
  background-repeat: no-repeat;
  border-radius: 10%;
  opacity: 0.8;
}

.vaccineName{
  font-family: 'Skia';
  font-size: 20px;
  color: white;
  line-height: 300px;
  height: 300px;
  width: 100%;
  text-align: center;
  display: block;
  margin: 0 auto;
  text-overflow: ellipsis;
  overflow: hidden;
}
